import React from "react";

const AboutMe = () => (
  <section className="tab-content about">
    <div className="typewriter">
      <h1>About me. </h1>
    </div>
    <p>I'm a web developer from London (currently based in Montréal) with over 4 years’ commercial experience building fast, beautiful web apps. I have 4 years experience working with Angular and 2 years experience working with React.</p>

    <div className="contact-links">
      <p>
        <span>Co-founder of: </span> 
        <span className="link">
          <a rel="noopener noreferrer" target="_blank" href="https://www.caterbase.co/#/home">Caterbase</a>  
          <span className="underline"></span>
        </span>
      </p>
      <p>
        <span>See some of my code: </span> 
        <span className="link">
          <a rel="noopener noreferrer" target="_blank" href="https://github.com/JoeDenny">GitHub</a>  
          <span className="underline"></span>
        </span>
      </p>
      <p>
        <span>Work history: </span> 
        <span className="link">
          <a rel="noopener noreferrer" target="_blank" href="https://www.linkedin.com/in/joedenny90/">LinkedIn</a>  
          <span className="underline"></span>
        </span>
      </p>
    </div>
  </section>
);

export default AboutMe;


