import React, {Component} from "react";
import { Link, Element } from 'react-scroll';

class AppHero extends Component {
  state = {
    scrollPosition: 0
  }

  componentDidMount = () => {    
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    
    if (window.scrollY >= 1000) {
      this.setState({ scrollPosition: 2 });
    } else if (window.scrollY > 100 && window.scrollY < 1000) {
      this.setState({ scrollPosition: 1 });
    } else {
      this.setState({ scrollPosition: 0 });
    }
    
  }

  render() {
    let userScrollClass = 'scroll-position-' + this.state.scrollPosition;
    return (
      <main>
        <div className="display-flex">
            <div className={"background-image " + userScrollClass}>
              <span className="scroll-arrows first-arrow">
                <Link activeClass="active" to="name" spy={true} smooth={true} offset={-250} duration={1300} onSetActive={this.handleSetActive}>
                  <ion-icon name="ios-arrow-round-down"></ion-icon>
                </Link>
              </span>
              <span className="scroll-arrows second-arrow">
                <Link activeClass="active" to="navigation" spy={true} smooth={true} offset={0} duration={2000} onSetActive={this.handleSetActive}>
                  <ion-icon name="ios-arrow-round-down"></ion-icon>
                </Link>
              </span>
            </div>
        </div>
        <div className={"hero-text text-right"}>
          <Element name="name" className="element"> 
            <div className="inner-box">
              <h1>Joe Denny</h1> 
              <p>Web developer based in Montréal</p>
            </div>
          </Element>
        </div>
      </main>
    );
  }
}
export default AppHero;


